$banner-img-path: 'https://qhomedesign-web.s3-ap-southeast-2.amazonaws.com/cdn_asset/';
    .banner {
      position: relative;
      width: 100%;
      height: 295px;
      background-image: url($banner-img-path +"images/home-banner.jpg");
      background-color: var(--overlay-02);
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      background-blend-mode: multiply;
      z-index: 2;
      &.bannerWithButton {
        height: 345px;
      }
      .bannerTitle {
        margin: 0;
        padding-top: 79px;
        font-family: var(--font-02);
        font-size: 36px;
        line-height: 1.15;
        text-align: center;
        color: var(--ui-01);
        z-index: 99;
      }
      .bannerContent {
        margin-top: 16px;
        font-family: var(--font-02);
        font-size: 20px;
        line-height: 1.3;
        text-align: center;
        color: var(--ui-01);
        z-index: 99;
      }
  
      .bannerButton {
        position: relative;
        width: 185px;
        height: 56px;
        margin-top: 27px;
        margin-left: calc(50vw - 106px);
        padding-left: 61px;
        border-radius: 6px;
        box-shadow: var(--shadow-01);
        border: none;
        outline: none;
        background-color: var(--brand-01);
        font-family: var(--font-family-02);
        font-size: 16px;
        line-height: 1.44;
        text-align: left;
        color: var(--ui-01);
        &:hover {
          cursor: pointer;
          background-color: var(--brand-hover);
        }
  
        .playButton {
          position: absolute;
          left: 28px;
          top: 16px;
          width: 0;
          height: 0;
          border-style: solid;
          border-width: 12px 0 12px 22px;
          border-color: transparent transparent transparent var(--ui-01);
        }
      }
    }