.footer {
  position: sticky;
  bottom: 0;
  width: 100%;
  height: var(--footerHeight);
  background-color: var(--ui-02);
  .footerBox {
    margin: 0 var(--footerHeight);
    border-top: 1px solid var(--ui-03);
    // display: flex;
    span {
      line-height: var(--footerHeight);
      padding-right: var(--footerHeight);
      text-align: center;
      display: inline-block;
      font-family: var(--font-01);
      color: var(--brand-01);
    }
    span a {
      line-height: var(--footerHeight);
      padding-right: var(--footerHeight);
      text-align: center;
      display: inline-block;
      font-family: var(--font-01);
      color: var(--brand-01);
    }
    span.footerSlogan {
      padding-right: 10px;
    }
    .footerLogo {
        img {
          height: var(--footerHeight);
        }
    }
  }
}
@media (max-width: 1024px) {
  .footer {
    display: none;
  }
}
