.contactInfo{
  background-color: #f6f6f6;
  height: 100vh;
  height: 800px;
  .contactTitle {
    font-family: Rubik;
    font-size: 18px;
    color: var(--brand-01);
    font-weight: 600;
    margin: 30px 0;
    text-decoration: underline;
  }
  .contactCategory {
    font-family: Rubik;
    font-size: 16px;
    color: var(--brand-01);
    font-weight: 600;
  }
  .contactDesc {
    font-family: Rubik;
    color: var(--brand-02);
    font-size: 16px;
  }
  .mapTitle {
    font-family: Rubik;
    font-size: 18px;
    color: var(--brand-01);
    font-weight: 600;
    margin: 30px 0 15px 0;
    text-decoration: underline;
    text-align: center;
  }
  .contactMap {
    width:100%;
    border: 1px solid #d6d6d6;
  }
}