body {
  background-color: #f6f6f6;
}
.tileBox {
  width: 100%;
  background-color: #f6f6f6;
  ul.tileTable {

    // border-collapse: separate;
    background-color: #f6f6f6;
    // overflow-x: auto;
    // margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-wrap: wrap;
    .homeTileCell {
      width: 400px;
      height: 300px;
      margin: 30px 0;
      border-radius: 3px;
      box-shadow: var(--shadow-01);
      background-color: var(--ui-01);
      &:hover {
        box-shadow: var(--shadow-02);
        cursor: pointer;
      }
      &:hover .homeTileViewReport {
        background-color: var(--brand-01);
        color: var(--ui-01);
      }

      .homeTileHeader {
        position: relative;
        width: 100%;
        height: 236px;
        p {
          margin: 0;
          padding: 0;
        }
        .homeTileImage {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 236px;
          overflow: hidden;
          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: transform 0.5s;
          }
          &:hover img {
            transform: scale(1.3);
          }
        }
      }
      .homeTileViewReport {
        // position: absolute;
        // bottom: 0;
        // left: 0;
        width: 100%;
        height: 64px;
        line-height: 64px;
        margin: auto;
        background-color: var(--ui-01);
        border-top: 1px solid var(--ui-02);
        border-radius: 0 0 3px 3px;
        opacity: 0.96;
        font-family: var(--font-02);
        color: var(--brand-01);
        text-align: center;
      }
    }
  }
}
ul, li {
  list-style: none;
  margin: 0;
  padding: 0;
}