.desktop {
  width: 100%;
  ul.dropdownMenu {
    position: relative;
    width: 100%;
    margin: 0 auto;
    padding: 0;
    border: none;
    height: var(--tabBarHeight);
    line-height: var(--tabBarHeight);
    background-color: var(--ui-01);
    text-align: center;
    vertical-align: middle;
    li {
      position: relative;
      display: inline-block;
      height: 100%;
      margin: 0;
      padding: 0 var(--tabButtonPadding);
      vertical-align: middle;
      text-align: center;
      font-family: var(--font-01);
      color: var(--brand-02);
      &:hover {
        color: var(--brand-01);
        border-bottom: solid var(--brand-01) var(--menuUnderlineHeight);
      }
      z-index: 99;
    }
  }

  ul.dropdownSubmenu {
    position: absolute;
    left: 0;
    top: calc(var(--tabBarHeight) + var(--menuUnderlineHeight));
    width: var(--dropMenuWidth);
    padding-left: 10px;
    background-color: var(--background-color-01);
    opacity: var(--backgound-opacity-01);
    display: none;
    text-align: center;
    a {
      display: block;
      text-decoration: none;
    }
    li {
      position: relative;
      display: block;
      text-decoration: none;
      margin: 0;
      padding: 0;
      height: var(--dropMenuLineHeight);
      line-height: var(--dropMenuLineHeight);
      width: 100%;
      text-align: left;
      color: var(--brand-02);
    }
  }

  .dropdownMenu li:hover {
    color: var(--brand-01);
  }

  .dropdownMenu li:hover ul {
    display: block;
  }

  .dropdownMenu li:hover ul ul {
    display: none;
  }

  .dropdownMenu li li:hover ul {
    display: block;
    color: var(--brand-01);
    left: 100%;
    top: 0;
  }

  .dropdownSubmenu li ul a li:hover {
    color: var(--brand-01);
  }

  li i {
    padding-left: var(--tabButtonIconPadding);
    &::before {
      padding-left: 5px;
      color: var(--brand-02);
      opacity: 0.8;
    }
  }
  div#logo {
    position: absolute;
    left: var(--logoLeft);
    top: 0;
    height: var(--tabBarHeight);
    display: flex;
    align-items: center;
    z-index: 1;
    img.menuLogo {
      display: block;
      height: var(--logoHeight);
    }
    img.menuFolder {
      display: none;
    }
  }
}
.mobile {
  width: 100%;
  div.tabBar {
    width: 100%;
    position: relative;
    height: var(--mobileTabBarHeight);
    background-color: var(--brand-01);
    div#logo {
      position: absolute;
      left: var(--logoLeft);
      top: 0;
      height: var(--mobileTabBarHeight);
      display: flex;
      align-items: center;
      z-index: 1;
      img.menuLogo {
        display: none;
      }
      img.menuFolder {
        display: block;
        height: var(--folderHeight);
      }
    }

    ul.dropdownMenu {
      display: none;
      position: absolute;
      top: 60px;
      left: 0;
      width: 250px;
      padding-left: 20px;
      background-color: var(--brand-02);
      opacity: 0.9;
      color: var(--ui-01);
      font-family: var(--font-01);
      z-index: 99;
      li {
        color: var(--ui-01);
        font-family: var(--font-01);
        font-size: 18px;
        ul.dropdownSubmenu {
          padding-left: 20px;
        }
      }
      li i {
        padding-left: 8px;
        // color: var(--brand-01);
      }
    }
  }
}
